#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 80vw;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 100px auto;
  grid-row-gap: 20px;
}
/* HEADER */
.header{
    display: grid;
    width: 34%;
    justify-self: center;
    grid-row-gap: 13px;
}
.project_name{
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;

    font-weight: 600;
    font-size: xx-large;
}
.menu{
    display: grid;
}
.menu .menu_items{
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;
}
.menu button{
    border: none;
    border-radius: 25px;
    width: 100px;
    padding: 7px;
    font-size: larger;
    cursor: pointer;
}
.menu button.active{
    color: #ffffff;
    background: #00bcd4;
    -webkit-box-shadow: 1px 1px 8px 0px #1f1e1e;
            box-shadow: 1px 1px 8px 0px #1f1e1e;
}
.menu button:focus{
    outline: none;
}
.menu button:not(.active):hover{
    -webkit-box-shadow: 1px 1px 1px 1px #adaaaa;
            box-shadow: 1px 1px 1px 1px #adaaaa;
}
/* Conversion list modal */
.list_of_conversions{
  display: grid;
}
.list_of_conversions .title{
  justify-self:center;
  font-size: x-large;
}
.list_items{
  padding: 8px;
  text-align: center;
}
.list_items:nth-child(even){
  background: #bdbcbc;
}


















































































































































































/* DROPZONE AREA */
.dropzone_area{
    display: grid;
    grid-template-rows: 200px auto;
    background: #bdbdbd;
    border-style: dashed;
    border-color: #828282;
}
.dropzone_text_hint{
    -ms-flex-item-align: center;
        align-self: center;
    justify-self:center;

    font-family: fantasy;
    font-size: xx-large;
    color: #e4e4e4;
    cursor: pointer;
}
.epub_list{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: minmax(150px, auto);
    grid-gap: 10px; 
    margin: 10px;
}
.epub_list:hover {
    cursor: auto;
}
.epub{
    display: grid;

    background: #e6e6e6;
    border:1px solid white;
}
.epub .btns{
    width: 24px;
    height: 24px;
}
.epub .btns:hover{
    cursor: pointer;
    stroke: #00bcd4;
}
.epub .btns:active{
    stroke: initial;
}
.epub .show_conversions{
    grid-column-start: 1;
    grid-row-start: 1;
    margin: 4px;
}
.epub .delete{
    grid-column-start: 2;
    justify-self: end;
    margin: 4px;
}
.epub .title{
    grid-column-start: 1;
    grid-column-end: 3;
    justify-self: center;
    padding: 10px;
}
.epub .download{
    grid-column-start: 1;
    grid-column-end: 3;
    justify-self: center;
}
.epub .loader{
    justify-self:center;
    grid-column-start: 1;
    grid-column-end: 3;
}
